

.main {
    background: url(../img/main-bg.jpg) center / cover no-repeat;
    @media (max-width: 766px) {
        padding: 100px 0 100px;
    }
    &__content {
       margin: 240px 0 215px;
       flex: 0 0 48%;
       @media (max-width: 900px) {
        margin: 100px 0 150px;
       }
       @media (max-width: 766px) {
        flex: 0 0 100%;
        margin: 0;
        text-align: center;
    }
    }

    

    &__subtitle {
        @include subtitle;
        margin-bottom: 12px;
    }

    &__header {
        @include headerTitle;
        margin-bottom: 14px;
        // &::after {
        //     content: url(../img/decore.svg);
        //     position: absolute;
        //     top: -10px;
        //     right: 70px;
        // }
        @media (max-width: 1100px) {
            font-size: 40px;
        }

    }

    &__photo {
        position: absolute;
        max-width: 784px;
        right: -120px;
        top: 190px;
        @media (max-width: 1391px) {
            max-width: 680px;
            top: 170px;
            right: -30px;
        }
        @media (max-width: 1210px) {
            max-width: 600px;
            top: 200px;
            right: 0;
        }
        @media (max-width: 900px) {
            max-width: 450px;
            top: 150px;
            right: 0;
        }
        @media (max-width: 766px) {
            max-width: 100%;
            position: relative;
            top: 0;
        }
    }
}
.main-inner {
    display: flex;
    align-items: center;
    position: relative;
    @media (max-width: 766px) {
        flex-direction: column;
        gap: 60px;
    }
}
.text {
    @include text;
    margin-bottom: 50px;
    @media (max-width: 1100px) {
       max-width: 300px;
    }
    @media (max-width: 766px) {
        margin: 0 auto;
        margin-bottom: 50px;
     }
}
.btn-main {
    @include btnRed;
    @media (max-width: 900px) {
        padding: 15px 20px;
    }
}
