.section-about {
  padding: 77px 0 120px;
  @media (max-width: 550px) {
    padding: 50px 0 70px;
  }
}
.about {
  display: flex;
  flex-direction: column;
  gap: 30px;
  &__card {
    display: flex;
    align-items: center;
    gap: 82px;
    @media (max-width: 550px) {
      flex-direction: column;
      gap: 30px;
    }
  }
  &__card--2 {
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    @media (max-width: 550px) {
      flex-direction: column;
      gap: 30px;
    }
  }

  &__card-img {
    width: 45%;
    @media (max-width: 550px) {
      width: 100%;
    }
  }

  &__card-info {
    max-width: 396px;
    @media (max-width: 766px) {
      width: 55%;
    }
    @media (max-width: 550px) {
      width: 100%;
    }
  }

  &__card-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
    color: #000000;
    margin-bottom: 23px;
    @media (max-width: 766px) {
      font-size: 20px;
    }
  }

  &__card-text {
    @include text;
    margin-bottom: 27px;
  }

  &__card-link {
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    color: #000000;
  }
}
