@import "https://fonts.googleapis.com/css2?family=Lato&family=Libre+Franklin:wght@400;700&family=Poppins:wght@400;700&display=swap";
* {
  border: 0;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

:focus, :active, a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-size: 14px;
  line-height: 1;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a, a:visited, a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

body {
  font-family: Poppins, sans-serif;
}

.stopscroll {
  overflow: hidden;
}

a {
  color: inherit;
}

img {
  width: 100%;
  height: auto;
}

.header {
  z-index: 11;
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.container {
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 15px;
}

.header-inner {
  justify-content: space-between;
  align-items: center;
  padding: 30px 0;
  display: flex;
}

@media (max-width: 768px) {
  .header-inner {
    padding: 10px 0;
  }
}

.header__logo {
  margin-right: auto;
}

.nav {
  margin-left: auto;
  margin-right: 40px;
}

.nav__menu {
  align-items: center;
  gap: 40px;
  padding: 10px 0;
  display: flex;
  position: relative;
}

.nav__menu:after {
  content: "";
  width: 2px;
  height: 100%;
  background: #ebebeb;
  position: absolute;
  top: 0;
  right: -20px;
}

.nav__link {
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #6c6c6c;
  font-family: Poppins;
  font-size: 12px;
  line-height: 22px;
  transition: all .3s;
}

.nav__link:hover, .nav__link.active {
  color: #ac1313;
}

@media (max-width: 768px) {
  .nav {
    height: 100vh;
    opacity: 0;
    width: 100vw;
    z-index: 10;
    background-color: #fff;
    transition: all .3s;
    position: fixed;
    bottom: -100%;
    left: 0;
  }

  .nav a {
    font-size: 40px;
  }

  .nav__menu {
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 40px;
  }

  .nav__menu-item:not(:last-child) {
    margin-bottom: 40px;
  }

  .nav.active {
    opacity: 1;
    bottom: 0;
  }
}

.btn-cart {
  background: none;
  margin-left: auto;
  padding: 5px 10px;
  position: relative;
}

.btn-cart img {
  pointer-events: none;
}

.btn-cart span {
  width: 15px;
  height: 15px;
  color: #fff;
  z-index: 1;
  background-color: #780c0c;
  border-radius: 400px;
  justify-content: center;
  font-size: 10px;
  transition: all .3s;
  display: inline-flex;
  position: absolute;
  top: 6px;
  right: -4px;
  transform: translate(-50%, -50%);
}

.header__btns {
  align-items: center;
  display: flex;
}

.burger {
  cursor: pointer;
  z-index: 10;
  flex: 0 0 20px;
  margin-left: 15px;
  display: none;
}

@media (max-width: 768px) {
  .burger {
    display: block;
  }
}

.bar {
  width: 25px;
  height: 3px;
  background-color: #6c6c6c;
  border-radius: 10px;
  margin: 5px auto;
  transition: all .3s ease-in-out;
  display: block;
}

@media (max-width: 768px) {
  .burger.active .bar:nth-child(2) {
    opacity: 0;
  }

  .burger.active .bar:nth-child(1) {
    transform: translateY(8px)rotate(45deg);
  }

  .burger.active .bar:nth-child(3) {
    transform: translateY(-8px)rotate(-45deg);
  }
}

.fade-block {
  z-index: 12;
  background: #0000004d;
  padding: 10px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.hidden {
