.section-shopping {
    background: url(../img/section-bg.png) center / cover no-repeat;
	padding: 120px 0 120px;
	@media (max-width: 500px) {
		padding: 60px 0 60px;
	}
}
.shopping {

		&__content {
			max-width: 500px;
			@media (max-width: 766px) {
				display: flex;
				flex-direction: column;
				align-items: center;
				margin: 0 auto;
			}
		}

		&__subtitle {
			@include subtitle;
			margin-bottom: 7px;
		}

		&__title {
			@include title;
			font-size: 36px;
			margin-bottom: 15px;
			@media (max-width: 766px) {
				text-align: center;
				font-size: 35px;
			}
		}

		&__text {
			font-family: 'Libre Franklin';
			font-weight: 700;
			font-size: 16px;
			line-height: 24px;
			color: #6C6C6C;
			margin-bottom: 30px;
			a {
				color: #AC1313;
				text-decoration: underline;
			}
			@media (max-width: 766px) {
				text-align: center;
			}
		}
}
.btn-red {
	@include btnRed;
}



