.section-contact {
    padding: 120px 0 120px;
    @media (max-width: 550px) {
        padding: 60px 0 60px;
    }
}
.contact {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

		&__subtitle {
            @include subtitle;
            margin-bottom: 40px;
		}

		&__title {
            @include title;
            text-align: center;
            position: relative;
            margin-bottom: 60px;
          @media (max-width: 766px) {
            font-size: 35px;
          }
		}

		&__form {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
		}
}
.input-wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    @media (max-width: 530px) {
         flex-direction: column;
         gap: 20px;       
    }
}
.form {

		&__input {
            @include input;
            width: 320px;
            @media (max-width: 530px) {
                width: 100%;
            }
		}

		&__check {
            margin-top: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 10px;
            position: relative;
            label {
                display: flex;
                align-items: center;
                gap: 10px;
                cursor: pointer;
            }
            label input {
                display: none;
            }
            label input:checked + .fake::before {
                display: block;
            }
            .fake {
                position: relative;
                width: 18px;
                height: 18px;
                border: 1px solid #50514f;
            }
            .fake::before {
                content: url(../img/select=checkbox.svg);
                position: absolute;
                display: none;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
		}

}
.btn-form {
    @include btnBlack;
}
