.section-category {
      padding: 120px 0 120px;
      border-bottom: 1px solid #EBEBEB;
      @media (max-width: 550px) {
            padding: 60px 0 60px;
      }
}
.subtitle {
    @include subtitle;
    text-align: center;
    margin-bottom: 40px;
}
.title {
    @include title;
    text-align: center;
    margin-bottom: 63px;
//     &::after {
//       content: url(../img/decore.svg);
//       position: absolute;
//       right: 45%;
//       top: -20px;
//       }
      @media (max-width: 900px) {
            font-size: 35px;
      }

}
.category {

		&__img {
            display: flex;
            justify-content: space-evenly;
            flex: 0 0 240px;
            margin-bottom: 90px;
		}

		&__item {
                  width: 21%;
		}

		&__description {
            display: flex;
            justify-content: space-between;
            @media (max-width: 550px) {
                  flex-direction: column;
                  gap: 30px;
            }
		}

		&__description-item {
            width: 42%;
            @media (max-width: 550px) {
                  width: 100%;
                  text-align: center;
            }
		}

		&__description-title {
                  font-weight: 700;
                  font-size: 29px;
                  line-height: 36px;
                  color: #000000;
                  margin-bottom: 26px;
                  position: relative;
                  @media (max-width: 766px) {
                        font-size: 23px;
                  }
		}
            &__description-text {
            font-size: 14px;
            line-height: 24px;
            color: #6C6C6C;
            }
}
