.fade-block {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba($color: #000000, $alpha: .3);
    z-index: 12;
    padding: 10px;
}
.hidden {
    display: none;
}
.modal-window {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #F9F7FC;
    border-radius: 24px;
    overflow: hidden; 
}


.shoping-cart {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: white;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    min-width: 450px;
    min-height: 200px;
    z-index: 10;
    display: none;
    padding: 20px;
    @media (max-width: 500px) {
        min-width: 300px;
    }
}
.active {
    display: block;
}
.shopping-cart {

		&__title {
            @include title;
            font-size: 20px;
            text-align: center;
            margin-bottom: 30px;
		}

		&__container {
            min-height: 100px;
            max-height: 500px;;
            display: flex;
            flex-direction: column;
            gap: 20px;
            border-bottom: 2px solid black;
            margin-bottom: 30px;
            overflow-y: auto;
            @media (max-width: 500px) {
                max-height: 300px;;
            }
		}
}
.btn-close {
    @include btnRed;
    padding: 10px 15px;
}
.btn-remove {
    @include btnRed;
    padding: 5px 10px;
}

.shopping-cart__container {
    .cart-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;
        img {
            width: 150px;
            height: 200px;
        }
        &__info {
            display: flex;
            flex-direction: column;
            gap: 10px;

        }
        &__title {

        }
        &__price {

        }
        &__amount {
            span {
                
                font-weight: bold;
            }
        }
        .btn-remove {

        }
    }
}

#empty {
    font-size: 24px;
    text-align: center;
}