.header {
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 11;
}
.container {
    max-width: 1180px;
    margin: 0 auto;
    padding: 0 15px;
}
.header-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px 0;
    @media (max-width: 768px) {
        padding: 10px 0;
    }
}
.header__logo {
    margin-right: auto;
}

.nav {
    margin-left: auto;
    margin-right: 40px;
    &__menu {
        display: flex;
        align-items: center;
        gap: 40px;
        position: relative;
        padding: 10px 0;
        &::after {
            content: '';
            position: absolute;
            width: 2px;
            height: 100%;
            background: #EBEBEB;
            top: 0;
            right: -20px;
        }
    }

    &__menu-item {
    }

    &__link {
        @include navLinks;
        transition: .3s;
        &:hover {
            color: #AC1313;
        }
        &.active {
            color: #AC1313;
        }
    }
    @media (max-width: 768px) {
        position: fixed;
        background-color: white;
        left: 0;
        bottom: -100%;
        height: 100vh;
        opacity: 0;
        width: 100vw;
        transition: 0.3s;
        z-index: 10;
        a {
            font-size: 40px;
        }
        &__menu {
            flex-direction: column;
            justify-content: center;
            gap: 40px;
            align-items: center;
            height: 100vh;
        }
        &__menu-item:not(:last-child) {
            margin-bottom: 40px;
        }
        &.active {
            opacity: 1;
            bottom: 0;
        }
    }
}






.btn-cart {
    background: none;
    padding: 5px 10px;
    margin-left: auto;
    position: relative;
    img {
        pointer-events: none;
    }
    span {
        position: absolute;
        width: 15px;
        height: 15px;
        display: inline-flex;
        right: -4px;
        top: 6px;
        transform: translate(-50%, -50%);
        background-color: #780c0c;
        justify-content: center;
        color: white;
        font-size: 10px;
        border-radius: 400px;
        transition: all .3s ease;
        z-index: 1;
    }
}

.header__btns {
    display: flex;
    align-items: center;
}

.burger {
    display: none;
    cursor: pointer;
    flex: 0 0 20px;
    margin-left: 15px;
    z-index: 10;
    @media (max-width: 768px) {
        display: block;
    }
}
.bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    background-color: #6C6C6C;
    border-radius: 10px;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}
@media (max-width: 768px) {
    .burger.active .bar:nth-child(2) {
        opacity: 0;
    }
    .burger.active .bar:nth-child(1) {
        transform: translateY(8px) rotate(45deg);
    }
    .burger.active .bar:nth-child(3) {
        transform: translateY(-8px) rotate(-45deg);
    }
}