.footer {
    background: rgba(219, 219, 219, 0.1);
    

    &_content-title {
        font-family: 'Poppins';
        font-size: 13px;
        line-height: 23px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #000000;
    }

    &_content-link {
        font-family: 'Libre Franklin';
        font-size: 14px;
        line-height: 24px;
        text-align: center;
        color: #6C6C6C;
        
    }

    &__logo {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 30px;
        padding-top: 120px;
        margin-bottom: 70px;
        img {
            width: 109px;
            height: 40px;
        }
        @media (max-width: 766px) {
            padding-top: 70px;
        }
    }

    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 70px;
        img {
            width: 40px;
            height: 40px;
            margin-bottom: 13px;
        }
        @media (max-width: 550px) {
            flex-direction: column;
            gap: 30px;
        }
    }

    &__content-item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

    }
}
.logo {
    &__desc {
        font-family: 'Libre Franklin';
        font-size: 14px;
        line-height: 24px;
        text-align: center;
        color: #6C6C6C;
        
    }
}

.footer {


    &__bottom {
        background: rgba(219, 219, 219, 0.1);
        padding: 30px 0 20px;
        text-align: center;
        border-top: 1px solid #EBEBEB;;

    }

    &__bottom-text {
        font-family: 'Libre Franklin';
        font-size: 12px;
        line-height: 22px;
        color: #6C6C6C;
        a {
            color: #000000;
            
        }
    }
}

