.section-products {
    
}
.products {
     padding: 120px 0 120px;
      @media (max-width: 766px) {
            padding: 70px 0 70px;
      }
		&__subtitle {
            @include subtitle;
            margin-bottom: 17px;
            text-align: center;
		}

		&__title {
            @include title;
            margin-bottom: 63px;
            text-align: center;
            @media (max-width: 766px) {
                  font-size: 35px;
            }
		}

		&__cards {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;
            margin-bottom: 60px;
            @media (max-width: 766px) {
                  grid-template-columns: repeat(2, 1fr);
            }
            @media (max-width: 386px) {
                  grid-template-columns: repeat(1, 1fr);
                  
            }
		}

		&__cards-item {
            max-width: 371px;
            height: auto;
            border-bottom: 1px solid #DBDBDB;
            padding-bottom: 15px;
		}

		&__photo {
            position: relative;
            margin-bottom: 20px;
            &:hover .products__btn-wrapper {
                cursor: pointer;
                opacity: 1;    
            }
		}

		&__btn-wrapper {
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background: rgba(159, 157, 157, 0.6);
            display: flex;
            justify-content: center;
            align-items: center;
            opacity: 0;
            transition: .3s;
		}
}
.btn-add {
    @include btnRed;
    @media (max-width: 700px) {
      padding: 10px 15px;
}
}
.product {

		&__desc {
            display: flex;
            align-items: center;
            justify-content: space-between;
		}

		&__desc-info {
		}

		&__name {
            font-family: 'Libre Franklin';
            font-size: 16px;
            line-height: 26px;
            color: #6C6C6C;
            @media (max-width: 500px) {
                  font-size: 14px;
            }
            
		}

		&__price {
            font-family: 'Poppins';
            font-size: 14px;
            line-height: 24px;
            color: #000000;
            
		}

		&__desc-amount {
            display: flex;
            align-items: center;
            gap: 10px;
		}
}
.items {

		&__contror {
            font-size: 20px;
            cursor: pointer;
		}

		&__counter {
            padding: 5px 10px;
            background: rgba(159, 157, 157, 0.6);
            

		}
}
.btn-procucts {
    @include btnRed;
    display: flex;
    margin: 0 auto;
}
